.productsHeading{
    margin: 4rem auto;
    font-size: 5rem;
    font-weight: 500;
    text-align: center;
    width: 30%;
    border-bottom: 0.1rem solid rgba(0,0,0,0.678);
    padding: 2rem;
    margin-bottom: 9rem;
}
.products{
    display: grid;
    grid-template-columns: repeat(4,1fr);
    gap: 1rem;
    padding-left: 30rem;
    width: 80%;
    box-sizing: border-box;
    min-height: 60rem;
}
.paginationBox{
    display: flex;
    justify-content: center;
    margin: 5rem 0;
}
.pagination{
    display: flex;
    justify-content: center;
}
.page-item{
    border: 0.1rem solid rgba(0,0,0,0.178);
    cursor: pointer;
    list-style: none;
    padding: 2rem 3rem;
    transition: all 0.5s;
}
.page-item:first-child{
    border-radius: 0.5rem 0 0 0.5rem;
}
.page-item:last-child{
    border-radius: 0 0.5rem 0.5rem 0;
}
.page-link{
    text-decoration: none;
    font-size: 2.5rem;
    font-weight: 300;
    color: rgb(80,80,80);
    transition: all 0.5s;
}
.page-item:hover{
    background-color: rgb(230,230,230);
}
.pageItemActive{
    background-color: tomato;
}
.pageLinkActive{
    color: white;
}
.productsFiltering{
    position: absolute;
    width: 20rem;
    top: 23rem;
    left: 5rem;
}
.productsFiltering .filterBox p{
    font-size: 2.5rem;
    font-weight: 500;
}
.productsFiltering .categorySection p{
    font-size: 2.5rem;
    font-weight: 500;
    margin-bottom: 1rem;
}
.productsFiltering .categorySection .categoryBox{
    margin-bottom: 1.5rem;
}
.productsFiltering .categorySection .categoryBox .category-link{
    list-style: none;
    font-weight: 300;
    font-size: 2rem;
    color: rgba(0,0,0,0.678);
    transition: all 0.5s;
    cursor: pointer;
}
.productsFiltering .categorySection .categoryBox .category-link:hover{
    color: tomato;
}
.productsFiltering .categorySection fieldset{
    padding: 1.2rem 2rem;
}
.productsFiltering .categorySection fieldset legend{
    font-size: 2.5rem;
    font-weight: 500;
}
.swal2-popup {
    font-size: 20px;
}
@media (max-width:1121px) {
    .products{
        grid-template-columns: repeat(3, 1fr);
    }
}
@media (max-width:868px) {
    .products{
        grid-template-columns: repeat(2, 1fr);
    }
}
@media (max-width:568px) {
    .products{
        grid-template-columns: 1fr;
    }
    .productsFiltering .filterBox p{
        font-size: 3rem;
    }
    .productsFiltering .categorySection p{
        font-size: 3rem;
    }
    .productsFiltering .categorySection .categoryBox .category-link{
        font-size: 2.5rem;
    }
    .productsFiltering .categorySection fieldset{
        padding: 0.5rem 2rem;
    }
    .productsFiltering .categorySection fieldset legend{
        font-size: 3rem;
    }
}

