.orderSuccess{
    margin: auto;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
}
.orderSuccess svg {
    font-size: 8rem;
    color: tomato;
}
.orderSuccess p{
    font-size: 4rem;
}
.orderSuccess a{
    text-decoration: none;
    font-size: 2.5rem;
    font-weight: 400;
    border: none;
    cursor: pointer;
    margin: 2rem;
    color: white;
    background-color: rgb(51,51,51);
    padding: 1rem 3rem;
}

@media (max-width:568px){
    .orderSuccess p{
        font-size: 4.5rem;
    }
    .orderSuccess svg{
        font-size: 8.5rem;
    }
    .orderSuccess a{
        font-size: 3rem;
    }
}