.orderDetailsPage{
    margin-top: 8rem;
    margin-bottom: 8rem;
    background-color: white;
}
.orderDetailsContainer{
    padding: 0 10rem;
}
.orderDetailsContainer > p{
    font-size: 3.5rem;
    font-weight: 500;
}
.orderDetailsContainer > h1{
    color: tomato;
    font-size: 3rem;
    margin-bottom: 2%;
}
.orderDetailsContainerBox{
    margin: 3rem 6rem; 
}
.orderDetailsContainerBox > div{
    display: flex;
    margin: 2rem 0;
    align-items: center;
}
.orderDetailsContainerBox div p{
    font-size: 2.5rem;
    font-weight: 400;
}
.orderDetailsContainerBox div span{
    margin: 0 2rem;
    font-size: 2rem;
    color: #575757;
}
.orderDetailsCartItems{
    padding: 0 10rem;
    padding-top: 2rem;
}
.orderDetailsCartItems p{
    font-size: 3.5rem;
    font-weight: 500;
}
.orderDetailsCartItemsContainer{
    max-height: 30rem;
    overflow-y: auto;
    margin: 3rem 6rem;
}
.orderDetailsCartItemsContainer div img{
    margin: 2rem 0;
    width: 7rem;
}
.orderDetailsCartItemsContainer div{
    display: grid;
    grid-template-columns: 2fr 3fr 4fr;
    align-items: center;
    font-size: 2rem;
    font-weight: 400;
}
.orderDetailsCartItemsContainer div a{
    color: #575757;
    text-decoration: none;
    cursor: pointer;
}
.orderDetailsCartItemsContainer div span{
    font-size: 2rem;
    font-weight: 600;
}
.swal2-popup {
    font-size: 20px;
}
@media (max-width:568px) {
    .orderDetailsContainer > p{
        font-size: 4rem;
    }
    .orderDetailsContainer > h1{
        font-size: 3.5rem;
    }
    .orderDetailsContainerBox div p{
        font-size: 3rem;
    }
    .orderDetailsContainerBox div span{
        font-size: 2.5rem;
    }
    .orderDetailsCartItems p{
        font-size: 4rem;
    }
    .orderDetailsCartItemsContainer div{
        font-size: 2.5rem;
    }
    .orderDetailsCartItemsContainer div span{
        font-size: 2.5rem;
    }
}
@media (max-width:490px) {
    .orderDetailsPage{
        margin-top: 20rem;
    }
}