.noOrders{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin: auto;
    height: 100vh;
    box-sizing: border-box;
}
.noOrders p{
    font-size: 5rem;
    margin-bottom: 3rem;
}
.orderListContainer{
    background-color: rgb(255,255,255);
    border-left: 1px solid rgba(0,0,0,0.13);
    margin: 0;
    padding: 0;
    width: 80vw;
}
#orderListHeading{
    font-size: 3rem;
    text-align: center;
    margin: 5rem;
}
table{
    table-layout: fixed;
}
.id{
    font-size: 0.9rem;
}
button:hover{
    color: tomato;
}
.swal2-popup {
    font-size: 20px;
}
@media (max-width:860px) {
    .orderListContainer{
        width: 100vw;
    }
}
@media (max-width:568px) {
    .noOrders p{
        font-size: 5.5rem;
    }
    #orderListHeading{
        font-size: 3.5rem;
    }
    .id{
        font-size: 1.4rem;
    }
}